.animation-wrapper {
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  max-height: 100vh;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.particle,
.particle:after {
  background: 0 0;
}
.particle:after {
  position: absolute;
  content: "";
  top: 2560px;
}
.particle-1 {
