.page-wrapper {
  text-align: center;
  color: #fff;
  z-index: 2;
}
.codepen-promo:active,
.codepen-promo:hover,
.codepen-promo:link,
.codepen-promo:visited {
  bottom: 30px;
  display: block;
  position: absolute;
  right: 30px;
  width: 200px;
  z-index: 99;
}
.codepen-promo-image {
  width: 100%;
}
