* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1 {
  margin: 0;
}

html, body {
  min-width: 320px;
  min-height: 568px;
  font-family: 'Noto Sans JP', sans-serif;
  overflow: scroll;
  overflow-x: hidden;
}

.container {
  max-width: 1300px;
  width: 100%;
  display: flex;
  animation: 1s fadein linear;
}

.slick-disabled {
  background-color: #4d4cc3;
}

.slick-list {
  padding: 20px;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, .1);
}

header {
  height: 90px;
  min-width: 320px;
  position: absolute;
  z-index: 100;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: .3s linear;
  }

  .dark-menu {
    background-color: rgba(0, 0, 0, 1);
    padding: 20px;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;

    li {
      color: #fff;
    }
  }

  #navigation {
    display: flex;
    justify-content: flex-start;
    color: #afb1b3;

    .nav-item {
      padding: 30px;
      font-size: 18px;
      cursor: pointer;

      &:hover {
        color: white;
      }
    }
  }

  .dropdown {
    display: none;
  }

  .header-right-block {
    color: #afb1b3;
    display: flex;
    align-items: center;

    .user {
      cursor: pointer;
      font-size: 25px;

      &:hover {
        color: white;
      }
    }

  }
}

.logo {
  a {
    font-size: 40px;
    text-decoration: none;
    color: #ffffff;
    text-transform: uppercase;
    font-weight: 700;

    span {
      background-image: linear-gradient(-225deg, #ff057c 0, #8d0b93 50%, #321575 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      margin: 0 5px;
      font-size: 45px;
    }
  }
}

main {
  position: relative;
  min-height: 568px;
}

.first {
  background-color: #161616;
  height: 100vh;
  max-height: 1080px;
  min-height: 568px;
  display: flex;
  justify-content: center;

  .container {
    //padding-top: 90px;
    //padding-bottom: 25px;
    padding: 90px 0 45px 0;
    flex-direction: column;
    justify-content: space-between;
  }
}

.first-top-block {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  flex: 1;


  .first-text-wrapper {
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #ffffff;

    span {
      font-size: 20px;
      color: #afb1b3;
      margin-bottom: 25px;
    }

    h1 {
      font-size: 50px;
      //margin-bottom: 45px;
    }
  }

  .hero-image {
    position: relative;
    border-radius: 5px;
    display: flex;
    width: 70%;
    //height: 70%;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: flex-start;
    font-size: 50px;
    color: #666666;
    background: transparent;
    background-size: cover;
    animation: .5s fadein linear;

    .icon {
      display: flex;
      position: relative;
      justify-content: flex-end;
      width: 15%;
      margin: 40px 0;

      &:first-child {
        i {
          animation: first-icon 10s linear infinite;
        }

        &:before {
          content: "barcode: #93212";
          padding: 5px 5px;
          font-size: 13px;
          position: absolute;
          top: -40px;
          color: white;

          border-radius: 15px;
          animation: first-code 10s linear infinite;
        }
      }

      &:last-child {
        i {
          animation: second-icon 10s linear infinite;
        }

        &:before {
          content: "barcode: #291345";
          padding: 5px 5px;
          font-size: 13px;
          position: absolute;
          top: -40px;
          color: #161616;
          background-color: white;
          border-radius: 15px;
          animation: second-code 10s linear infinite;
        }
      }


    }
  }
}

.first-bottom-block {
  display: flex;
  justify-content: space-between;
  color: #ffffff;
  animation: 1s appearFromBottom ease-in;

  .first-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    .first-picture {
      font-size: 50px;

      i {
        background-image: linear-gradient(-225deg, #ff057c 0, #8d0b93 50%, #321575 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }

    .first-description {
      h4 {
        font-size: 18px;
        margin: 11px 0;
      }

      p {
        font-size: 14px;
        color: #afb1b3;
      }
    }
  }
}

.second {
  width: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;

  .background-figure {
    position: absolute;
    z-index: -1;
    width: 100%;
    bottom: -5px;
    animation: 5s wavingWaves linear infinite;
  }

  .second-wave {
    z-index: -2;
    animation: 3s wavingWaves linear infinite;
  }

  .container {
    flex-direction: column;
    align-items: center;
    flex: 2;
    padding: 90px 0;
    height: 100%;

    h2 {
      opacity: 0;
      font-size: 36px;
      margin: 45px;
      color: #282936;
    }

    .slide-top {
      opacity: 1;
      animation: 1s fadein ease-in;
    }

    .second-container {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: flex-start;

      .wrapper-placeholder {
        opacity: 0;
        //width: 90%;
        //background-color: #f4f4f4;
        border-radius: 15px;
        margin-top: 45px;
        padding: 30px;
        color: #282936;
        //box-shadow: 1px 30px 30px 6px rgba(0, 0, 0, .1);

        ul {
          display: flex;
          flex-direction: column;
          //min-height: 200px;

          li {
            list-style-type: none;
            font-size: 20px;
            margin-bottom: 20px;
            padding: 20px;
            background-color: #161616;
            box-shadow: 0px 0px 17px -7px rgba(22, 22, 22, 1);
            color: #fff;
            border-radius: 15px;
          }
        }
      }

      .slide-bottom {
        animation: 1s appearFromBottom ease-in;
        opacity: 1;
      }


      .buttons-wrapper {
        opacity: 0;
        display: flex;
        justify-content: center;
        position: relative;

        #buttons-wrapper-active-button {
          position: absolute;
          width: 25%;
          height: 100%;
          left: 0;
          z-index: -1;
          background-image: linear-gradient(-225deg, #FF057C 0%, #8D0B93 50%, #321575 100%);
          transition: .5s;
          border-radius: 15px;
          box-shadow: 0px 0px 26px -5px rgba(50, 21, 117, 1);
        }

        .first-content, .second-content, .third-content, .fourth-content {
          display: flex;
          cursor: pointer;
          width: 25%;
          //overflow: hidden;


          .wrapper-content {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 35px;
            background-color: transparent;
            color: #282936;
            justify-content: space-between;
            //box-shadow: inset 1px 1px 60px 5px rgba(0, 0, 0, .1);
            overflow: hidden;
            transition: .4s;

            &:hover {
              transform: scale(1.1);
            }

            i {
              font-size: 45px;
              color: #282936;
              margin-bottom: 20px;
            }

            h4 {
              font-size: 20px;
              text-align: center;
              //margin-bottom: 30px;
            }
          }

          .active i, .active h4, .active p {
            color: #fff;
            transition: .45s;
          }
        }

        .first-content {

          .wrapper-content {
            border-radius: 15px;
          }
        }

        .fourth-content {

          .wrapper-content {
            border-radius: 0 15px 0 0;
          }
        }

      }

      .slide-top {
        opacity: 1;
        animation: 1s appearFromTop ease-in;
      }
    }
  }

}

.third {
  height: 110vh;
  width: 100%;
  position: relative;
  z-index: 1;
  background-color: #161616;
  display: flex;
  justify-content: center;

  .first-circle {
    position: absolute;
    right: -200px;
    top: -150px;
    width: 500px;
    height: 500px;
    z-index: -1;
    border-radius: 50%;
    box-shadow: 0px 0px 49px -9px rgba(141, 11, 147, 1);
    background-image: linear-gradient(-225deg, #FF057C 0%, #8D0B93 50%, #321575 100%);
    animation: 10s movingCircleX linear infinite;
  }

  .second-circle {
    position: absolute;
    left: -300px;
    top: 200px;
    width: 400px;
    height: 400px;
    z-index: -2;
    border-radius: 50%;
    box-shadow: 0px 0px 49px -9px rgba(178, 36, 239, 1);
    background-image: linear-gradient(to top, #b224ef 0%, #7579ff 100%);
    animation: 9s movingCircleX linear infinite;
  }

  .left-appear {
    animation: 1s leftCirclesAppear linear;
  }

  .third-circle {
    position: absolute;
    top: 500px;
    left: -150px;
    z-index: -1;
    width: 400px;
    height: 400px;
    border-radius: 50%;
    box-shadow: 0 0 40px -9px rgba(59, 65, 197, 1);
    background-image: linear-gradient(to top, #007adf 0%, #00ecbc 100%);
    animation: 10s movingCircleY linear infinite;
  }

  .fourth-circle {
    position: absolute;
    right: -200px;
    bottom: 50%;
    width: 400px;
    height: 400px;
    z-index: -1;
    border-radius: 50%;
    box-shadow: 0px 0px 49px -9px rgba(252, 96, 118, 1);
    background-image: linear-gradient(-20deg, #fc6076 0%, #ff9a44 100%);
    animation: 8s movingCircleY linear infinite;
  }

  .fifth-circle {
    position: absolute;
    right: -100px;
    bottom: -100px;
    width: 300px;
    height: 300px;
    z-index: -1;
    border-radius: 50%;
    box-shadow: 0px 0px 49px -9px rgba(33, 212, 253, 1);
    background-image: linear-gradient(-20deg, #b721ff 0%, #21d4fd 100%);
    animation: 6s movingCircleX linear infinite;
  }

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 65px 0;

    .circle-back {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      border-radius: 15px;
      color: white;
      height: 60%;
      width: 50%;
      opacity: 0;

      .circle-back-top {
        height: 230px;
        margin: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
      }

      h2 {
        font-size: 36px;
        //padding: 30px;
      }

      p {
        font-size: 20px;
        line-height: 36px;
        padding: 20px;
        background-color: rgba(255, 255, 255, .1);
        border-radius: 15px;

      }

      i {
        font-size: 120px;
        //padding: 30px;
        animation: 15s rotating linear infinite;
      }
    }


    .left-appear {
      animation: 1s leftAppear linear;
      opacity: 1;
    }

    .line {
      width: 2px;
      height: 200px;
      background-color: white;
      margin: 0 60px;

    }

    .third-wrapper {
      opacity: 0;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      height: 60%;
      width: 50%;

      .third-wrapper-top {
        height: 230px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        margin: 20px;
      }
    }

    .right-appear {
      opacity: 1;
      animation: 1s rightAppear linear;
    }
  }

  h3 {
    color: white;
    //margin-bottom: 35px;
    text-align: center;
    font-size: 20px;
    line-height: 25px;
    font-weight: 100;
  }

  .third-text-wrapper {
    display: flex;
    position: relative;
    justify-content: space-evenly;
    align-items: flex-start;

    p {
      color: white;
      border-radius: 15px;
      //padding: 30px 0;
      line-height: 25px;
      font-size: 20px;
    }
  }

  .slider-buttons {
    display: flex;
    justify-content: space-evenly;
    //margin-bottom: 35px;


    .left-btn {
      padding: 18px;
      font-size: 18px;
      background: white;
      border-radius: 15px;
      text-align: center;
      width: 150px;
      cursor: pointer;

      &:hover {
        background-color: #4d4cc3;
        color: white;
      }
    }

    .right-btn {
      padding: 18px;
      font-size: 18px;
      background: white;
      text-align: center;
      border-radius: 15px;
      width: 150px;
      cursor: pointer;

      &:hover {
        background-color: #4d4cc3;
        color: white;
      }
    }
  }

  .grey {
    color: #afb1b3;
    text-align: center;
    margin: 20px;
  }

  .gradient {
    color: #4d4cc3;
    font-style: italic;
    font-weight: bold;
  }
}

.sixth {
  display: flex;
  justify-content: center;
  height: 30vh;

  .container {
    flex-direction: column;
    justify-content: center;
    padding: 65px;

    h3 {
      font-family: 'Roboto', sans-serif;
      font-size: 32px;
      color: #666666;
      font-weight: 100;
      text-align: center;
      margin-bottom: 65px;
    }
  }
}

.partners {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.partner-logo {
  display: flex;
  justify-content: center;
  margin: 0 20px;

  img {
    width: 70px;
    height: 70px;
    transition: all 0.1s ease-in-out;
    filter: grayscale(100%);

    &:hover {
      filter: grayscale(0);
    }
  }
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #161616;
  height: 70vh;
}

.first-footer-section {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  background-image: linear-gradient(-225deg, #dfaeaf 0%, #4144c4 100%);

  .container {
    display: flex;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    color: white;
    font-size: 32px;


    #contact {
      font-size: 28px;
      font-weight: 100;
    }

    .socials {
      display: flex;
      height: 100%;
      width: 80px;
      justify-content: space-between;
      align-items: center;

      .facebook {
        color: white;

        &:hover {
          color: #4267b2;
        }

      }

      .linkedin {
        color: white;

        &:hover {
          color: #0073b0;
        }
      }
    }
  }
}

.second-footer-section {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #161616;
  flex: 1;

  .footer-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    //padding: 32px 0;

    .footer-item {
      width: 25%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      flex-wrap: wrap;

      .footer-items-wrap {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        color: white;
        padding: 0 32px;

        .logo {
          margin-bottom: 28px;

          a {
            font-size: 28px;
            font-weight: normal;
          }
        }

        .footer-title {
          text-decoration: none;
          color: white;
          font-size: 28px;
          margin-bottom: 28px;
        }

        .anchor {
          text-decoration: none;
          color: white;
          font-size: 14px;

          &:not(:last-child) {
            margin-bottom: 14px;
          }
        }
      }
    }
  }
}

.third-footer-section {
  display: flex;
  justify-content: center;

  .copyright {
    width: 100%;
    display: flex;
    justify-content: center;
    color: white;
    padding: 20px;
    font-size: 12px;
    background-color: #161616;
    border-top: 1px solid #f9748f;
  }
}


@keyframes rotating /* Safari and Chrome */ {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes movingCircleY {
  0% {
    transform: translateY(-10px);
  }
  25% {
    transform: translateX(-15px);
  }
  50% {
    transform: translateY(10px);
  }
  75% {
    transform: translateX(15px);
  }
  100% {
    transform: translateY(-10px);
  }
}

@keyframes movingCircleX {
  0% {
    transform: translateX(-10px);
  }
  25% {
    transform: translateY(-15px);
  }
  50% {
    transform: translateX(10px);
  }
  75% {
    transform: translateY(15px);
  }
  100% {
    transform: translateX(-10px);
  }
}

@keyframes leftAppear {
  from {
    transform: translateX(-200px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes rightAppear {
  from {
    transform: translateX(200px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}


@keyframes appearFromTop {
  from {
    display: none;
    transform: translateY(-200px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes appearFromBottom {
  from {
    transform: translateY(200px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes wavingWaves {
  30% {
    transform: translateY(10px);
  }
  60% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}


@keyframes first-icon {
  from, 10% {
    font-size: 50px;
    position: absolute;
    top: 0;
    bottom: 0;
    color: #666666;
    transform: scale(1);
  }
  13%, 20% {
    transform: scale(1.2);
    color: #ffffff;
  }
  23%, to {
    transform: scale(1);
    color: #666666;
  }
}

@keyframes first-code {
  from, 13% {
    transform: translateY(50%);
    opacity: 0;
  }
  16%, 20% {
    transform: translateY(0);
    opacity: 1;
  }
  23%, to {
    opacity: 0;
  }
}

@keyframes second-icon {
  from, 26% {
    font-size: 50px;
    position: absolute;
    top: 0;
    bottom: 0;
    color: #666666;
    transform: scale(1);
  }
  29%, 34% {

    transform: scale(1.2);
    color: #ffffff;
  }
  37%, to {
    transform: scale(1);
    color: #666666;
  }
}

@keyframes second-code {
  from, 29% {
    transform: translateY(50%);
    opacity: 0;
  }
  32%, 34% {
    transform: translateY(0);
    opacity: 1;
  }
  37%, to {
    opacity: 0;
  }
}

@media screen and (max-width: 1350px) {
  .first {
    min-height: 1066px;
  }

  .container {
    width: 750px;
  }

  .first-top-block {
    flex-direction: column;
    align-items: center;

    & br {
      display: none;
    }

    .first-text-wrapper {
      width: 100%;
      text-align: center;
    }

    .hero-image {
      width: 100%;
      justify-content: space-around;

      .icon {
        justify-content: center;
      }
    }

  }
}

@media screen and (max-width: 830px) {
  header {
    .container {
      position: relative;

      .dropdown {
        display: flex;
        position: absolute;
        width: 100%;
        flex-direction: column;

        .dropdown-button {
          background-color: transparent;
          display: flex;
          align-items: center;
          justify-content: flex-end;
          z-index: 1;

          .hamburger {
            display: flex;
            align-items: center;
            height: 10vh;
            color: white;

            &:focus {
              outline: none;
            }

            .hamburger-inner, .hamburger-inner::after, .hamburger-inner::before {
              background-color: white;
            }
          }
        }

        .dropdown-content {
          position: absolute;
          transition: top 0.5s;
          top: -1000px;
          width: 100%;
          display: flex;
          flex-direction: column;
          z-index: 2;
          box-shadow: 0 0 39px -5px rgba(0, 0, 0, 0.75);

          a {
            width: 100%;
            height: 15vh;
            text-align: center;
            text-decoration: none;
            color: black;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: white;

            &:hover {
              background-color: #000000;
              color: white;
            }

            span {
              font-size: 1.5rem;

              &:hover {
                color: white;
              }
            }
          }
        }
      }
    }

    .header-right-block {
      width: 100%;

      #navigation {
        display: none;
      }
    }
  }

  main {
    z-index: 0;

    .first-top-block {
      .hero-image {
