          .wrapper-content {
            i {
              font-size: 2rem;
            }

            h4 {
              font-size: 1rem;
            }
          }
        }
      }
    }
  }

  @keyframes first-icon {
    from, 10% {
      font-size: 50px;
      position: absolute;
      top: 0;
      bottom: 0;
      color: #666666;
      transform: scale(1);
    }
    13%, 20% {
      transform: scale(1.2);
      color: #ffffff;
    }
    23%, to {
      transform: scale(1);
      color: #666666;
    }
  }

