    }
    16%, 20% {
      transform: translateY(0);
      opacity: 1;
    }
    23%, to {
      opacity: 0;
    }
  }

  @keyframes second-icon {
    from, 26% {
      font-size: 50px;
      position: absolute;
      top: 0;
      bottom: 0;
      color: #666666;
      transform: scale(1);
    }
