      color: #ffffff;
    }
    37%, to {
      transform: scale(1);
      color: #666666;
    }
  }

  @keyframes second-code {
    from, 29% {
      transform: translateY(50%);
      opacity: 0;
    }
    32%, 34% {
      transform: translateY(0);
      opacity: 1;
    }
    37%, to {
      opacity: 0;
    }
  }
}


