  text-decoration: none
}


/*////////////////////////Button styling/////////////////*/
::after, ::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box
}

button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: 0;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: 0 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit
}

button.learn-more {
  width: 12rem;
  height: auto
}

button.learn-more .circle {
  -webkit-transition: all .45s cubic-bezier(.65, 0, .076, 1);
  -o-transition: all .45s cubic-bezier(.65, 0, .076, 1);
  transition: all .45s cubic-bezier(.65, 0, .076, 1);
  position: relative;
  margin: 0;
  width: 3rem;
  height: 3rem;
  background: #282936;
  border-radius: 1.625rem
}

button.learn-more .circle .icon {
  -webkit-transition: all .45s cubic-bezier(.65, 0, .076, 1);
  -o-transition: all .45s cubic-bezier(.65, 0, .076, 1);
  transition: all .45s cubic-bezier(.65, 0, .076, 1);
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  background: #fff
}

button.learn-more .circle .icon.arrow {
  -webkit-transition: all .45s cubic-bezier(.65, 0, .076, 1);
  -o-transition: all .45s cubic-bezier(.65, 0, .076, 1);
  transition: all .45s cubic-bezier(.65, 0, .076, 1);
  left: .625rem;
  width: 1.125rem;
  height: .125rem;
  background: 0 0
}

button.learn-more .circle .icon.arrow::before {
  position: absolute;
  content: '';
  top: -.25rem;
  right: .0625rem;
  width: .625rem;
  height: .625rem;
  border-top: .125rem solid #fff;
  border-right: .125rem solid #fff;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg)
}

button.learn-more .button-text {
  -webkit-transition: all .45s cubic-bezier(.65, 0, .076, 1);
  -o-transition: all .45s cubic-bezier(.65, 0, .076, 1);
  transition: all .45s cubic-bezier(.65, 0, .076, 1);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: .75rem 0;
  margin: 0 0 0 1.85rem;
  color: #282936;
  font-weight: 700;
  line-height: 1.6;
  text-align: center;
  text-transform: uppercase
}

button:focus .circle {
  width: 100%
}

button:focus .circle .icon.arrow {
  background: #fff;
  -webkit-transform: translate(1rem, 0);
  -ms-transform: translate(1rem, 0);
  transform: translate(1rem, 0)
}

button:focus .button-text {
  color: #fff
}


